#fabricTable {
  margin-top: 30px; }
  #fabricTable div.row {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0; }
  #fabricTable .tableHead {
    font-size: 16px;
    font-weight: 600;
    padding-bottom: 15px;
    margin-right: -17px; }
    #fabricTable .tableHead > div {
      padding-right: 17px; }
    #fabricTable .tableHead .sortable {
      cursor: pointer; }
  #fabricTable .tableBody {
    max-height: 600px;
    overflow-y: scroll;
    border-top: solid black 2px;
    border-bottom: solid black 2px; }
    #fabricTable .tableBody .row {
      padding-top: 5px;
      padding-bottom: 5px;
      height: 110px;
      cursor: pointer; }
      #fabricTable .tableBody .row:nth-child(even) {
        background-color: #f3f2f1; }
      #fabricTable .tableBody .row:nth-child(odd) {
        background-color: #fff; }
      #fabricTable .tableBody .row:hover {
        background-color: #dfdfdd; }

#paging {
  padding-right: 30px; }

#fabricDetailModal .form-group .row {
  margin-top: 15px; }
  #fabricDetailModal .form-group .row span.label {
    font-size: 16px;
    line-height: 2.3; }
  #fabricDetailModal .form-group .row .custom-control.custom-checkbox {
    margin-top: 5px; }
  #fabricDetailModal .form-group .row .btn.preview {
    height: 36px;
    margin-left: 20px; }
